<template>
  <div class="side-menu">
    <el-menu
      ref="side-menu"
      :router="true"
      :default-active="active"
      @select="selectMenuHandler"
    >
      <el-submenu
        v-for="item in menuData"
        :key="item.name"
        :index="item.path"
      >
        <template slot="title">
          <span slot="title">
            {{ item.title }}
          </span>
        </template>
        <el-menu-item
          v-for="children in item.children"
          :key="children.name"
          :index="children.path"
        >
          {{ children.title }}
        </el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>

<script>
import menuData from '@/router/routes.js';

export default {
    name: 'TheSideMenu',
    data() {
        return {
            menuData,
            active: this.$store.state.activeMenuIndex,
        };
    },
    created() {
        this.initMenu();
    },
    methods: {
        selectMenuHandler(index) {
            this.$store.commit('setState', { key: 'activeMenuIndex', value: index });
        },
        initMenu() {
            const getMenuItem = (items) => items
                .filter((item) => !item.meta.hidden)
                .map((item) => {
                    if (item.children && item.children.length > 0) {
                        return {
                            ...item.meta,
                            path: item.path,
                            name: item.name,
                            children: getMenuItem(item.children),
                        };
                    }
                    return {
                        ...item.meta,
                        path: item.path,
                        name: item.name,
                    };
                });
            this.menuData = getMenuItem(this.menuData);
        },
    },
};
</script>

<style lang="scss">
.side-menu {
  padding-bottom: 40px;
  background: #fff;
  & > .el-menu {
    border-right: none;
    & > li > .el-submenu__title {
      height: 56px;
      line-height: 56px;
      padding-left: 30px !important;
      color: rgba(153,153,153,1) !important;
      .iconfont-haistack {
        margin-right: 9px;
      }
      .el-submenu__icon-arrow {
        right: 30px !important
      }
    }
  }
  .el-menu {
    border-right: none;
    .el-icon-caret-right {
      margin-right: 0px !important;
    }
    li.el-submenu.is-opened i {
      transform: rotate(90deg);
      transition: all 0.5s;
    }
    li.el-submenu.is-opened {
      .el-icon-arrow-down::before {
        content: "\e6de";
      }
    }
    li.el-submenu i {
      transform: rotate(0deg);
      transition: all 0.5s;
    }
  }
  .el-submenu__title {
    height: 32px;
    line-height: 32px;
    font-size: 16px;
    &:hover {
      background: rgb(244, 248, 251);
    }
    .iconfont-haistack {
      vertical-align: 0;
    }
  }
  .el-menu-item:hover,
  .el-menu-item:focus {
    outline: none;
    color: blue;
    background: none;
  }
  .el-menu-item:hover {
    color: blue;
  }
  .el-menu-item {
    height: 50px;
    line-height: 50px;
    padding-left: 55px !important;
    font-weight: 500;
    color: rgba(34, 34, 34, 1);
  }
  .el-menu-item.is-active {
    background: rgb(244, 248, 251);
    color: blue;
    font-weight: 600;
    &::after {
      content: "";
      border-right: 3px solid blue;
      position: absolute;
      right: 0px;
      height: 16px;
      top: 17px;
    }
  }

  .tohome {
    margin: 0px 30px;
    padding: 34px 30px 32px 30px;
    background: #fff;
    border-bottom: 1px solid #eee;
    a {
      display: block;
      font-size: 18px;
      color: green;
      font-weight: 600;
      .icon-storage-cangchuguanli {
        font-size: 23px;
        font-weight: 200;
        position: relative;
        top: 2px;
      }
      .icon-common-angle-right {
        color: #bbb;
        position: relative;
        left: 10px;
        font-weight: bold;
        font-size: 11px;
      }
      &:hover,
      &.active {
        .icon-common-angle-right {
          color: yellow;
        }
      }
    }
  }
}
</style>
