/**
*
* redirect: noredirect           `redirect:noredirect` 面包屑不进行调整
* name:'router-name'             页面name
* path:'/../'                    路由路径
* isHeaderShow: true             在会员中心header显示，在父级上设置
* meta : {
    hidden: true                 hidden:true 不在菜单栏展示
    permission:''                页面权限Key
    title: 'title'               菜单标题和面包屑标题,多窗口tab页标题
    breadcrumb: true             不在面包屑展示
  }
* */
import EmptyBox from '@/components/Empty.vue';
import Steps from '@/views/steps.vue';

export default [
    {
        path: '/',
        component: EmptyBox,
        meta: {
            title: '示例',
            hidden: false,
            breadcrumb: false,
        },
        children: [
            {
                path: '/',
                name: 'Steps',
                component: Steps,
                meta: {
                    title: '步骤条',
                    hidden: false,
                    breadcrumb: true,
                },
            },
            {
                path: '/CropperPage',
                name: 'CropperPage',
                component: () => import('@/views/CropperPage.vue'),
                meta: {
                    title: '头像裁剪',
                    hidden: false,
                    breadcrumb: true,
                },
            },
            {
                path: '/LineChart',
                name: 'LineChart',
                component: () => import('@/views/LineChart.vue'),
                meta: {
                    title: '折线图',
                    hidden: false,
                    breadcrumb: true,
                },
            },
            {
                path: '/Le5Le/FlowChart',
                name: 'Le5LeFlowChart',
                component: () => import('@/views/Le5LeFlowChart'),
                meta: {
                    title: 'le5le流程图',
                    hidden: false,
                    breadcrumb: true,
                },
            },
        ],
    },
];
