import Vue from 'vue';
import Vuex from 'vuex';
import persistedState from 'vuex-persistedstate';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        activeMenuIndex: '/',
    },
    mutations: {
        setState(state, payload) {
            state[payload.key] = payload.value;
        },
    },
    actions: {
    },
    modules: {
    },
    plugins: [persistedState()],
});
